.app__work-filter{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    margin: 4rem 0 2rem;

    .app__work-filter-item{
        padding: 0.5rem 1rem;
        border-radius: 0.5rem;
        background-color: var(--title-color);
        color: #fff;
        font-weight: 800;
        cursor: pointer;
        transition: all .3s ease;
        margin: .5rem;
        &:hover{
            background-color: var(--title-color-dark);
            color: #fff;
        }

        @media screen and (min-width: 2000px) {
            padding: 1rem 2rem;
            border-radius: .85rem;
        }
    }

    .item-active{
        background-color: var(--title-color);
        color: #fff;

    }
}